const FIRST_ID = 0;

export default {
  actions: {
    initSlider({commit, dispatch}, {dataUrl, visibleCount, centerOffset}) {
      fetch(dataUrl)
          .then(response => {
            if (response.ok) {
              return response.json();
            } else {
              throw new Error(`response status: ${response.status}`);
            }
          })
          .then(data => {
            commit('setPictures', data.pictures);
            commit('setSlider', {
              centerOffset,
              visibleCount,
              lastId: data.pictures.length - 1
            });
            dispatch('slideTo', 0);
          })
          .catch(e => {
            console.log(`Error in Picture component – ${e}`)
          })
    },
    slideTo({commit, state}, increment) {
      const nextId = state.show.currentId + increment
      if (nextId < 0 || nextId > state.slider.lastId) {
        return;
      }

      let minId, maxId = 0;
      let startId = state.show.startId;
      if (nextId > state.show.currentId) {
        minId = Math.max(FIRST_ID, state.show.currentId - state.slider.centerOffset);
        maxId = Math.min(state.slider.lastId, nextId + state.slider.centerOffset);
      } else {
        minId = Math.max(FIRST_ID, nextId - state.slider.centerOffset);
        maxId = Math.min(state.slider.lastId, state.show.currentId + state.slider.centerOffset);
      }
      if (nextId < state.slider.centerOffset) {
        maxId = state.slider.visibleCount;
        startId = FIRST_ID;
      } else if (nextId > state.slider.lastId - state.slider.centerOffset) {
        minId = state.slider.lastId - state.slider.visibleCount;
        startId = minId + 1;
      } else {
        startId = nextId - state.slider.centerOffset;
      }

      const list = [];
      for (let i = minId; i <= maxId; i++) {
        list.push(state.pictures[i]);
      }

      commit('setShow', {
        minId,
        startId,
        list,
        currentId: nextId
      });
    }
  },
  mutations: {
    setPictures(state, payload) {
      state.pictures = payload;
    },
    setSlider(state, payload) {
      state.slider = payload;
    },
    setShow(state, payload) {
      state.show = payload;
    }
  },
  state: {
    pictures: [],
    slider: {
      centerOffset: 2,
      lastId: Infinity,
      visibleCount: 5
    },
    show: {
      minId: 0,
      startId: 0,
      currentId: 0,
      list: []
    }
  },
  getters: {
    getShow(state) {
      return state.show;
    },
    getInfoData(state) {
      return {
        isLeftLimit: state.show.minId === state.show.currentId,
        isRightLimit: state.slider.lastId === state.show.currentId,
        slideCapture: state.pictures.length ? state.pictures[state.show.currentId].name : ``,
        slideNumber: state.pictures.length ? state.show.currentId + 1 : 0,
        slidesTotal: state.pictures.length ? state.slider.lastId + 1 : 0
      };
    }
  }
}
