import _ from 'lodash';

export default {
  actions: {
    initFilterKeys({commit, dispatch}, {optionFilters, rangeFilter}) {
      commit('setFilterKeys', {optionFilters, rangeFilter});
    },
    fetchData({commit, dispatch}, url) {
      fetch(url)
          .then(response => {
            if (response.ok) {
              return response.json();
            } else {
              throw new Error(`error - ${response.status}`);
            }
          })
          .then(data => {
            commit('setPictures', data.pictures);
            commit('setShowedPictures', data.pictures);
            dispatch('__mapOptions');
            dispatch('__resetFilters');
          })
          .catch(e => {
            console.log(`Fetch throw error in Gallery: `, e)
          });
    },
    __resetFilters({commit, state}) {
      const rangeFilter = state.filterKeys.rangeFilter;
      const checkedFilters = {};
      state.filterKeys.optionFilters.forEach(key => {
        checkedFilters[key] = [];
      });
      checkedFilters[rangeFilter] = {min: 0, max: Infinity, checkedId: -1};
      commit('setCheckedFilters', checkedFilters);
    },
    __mapOptions({commit, state}) {
      const result = {};
      let id;
      // Сбор данных (поиск уникальных имен опций и подсчет их колличества)
      state.pictures.forEach(picture => {
        state.filterKeys.optionFilters.forEach(key => {
          if (!result[key]) {
            result[key] = [];
          }
          id = _.findIndex(result[key], it => it.value === picture[key]);
          if (id < 0) {
            result[key].push({value: picture[key], count: 1});
          } else {
            result[key][id].count++;
          }
        });
      });
      // Сортировка по убыванию на основе частоты появления орпций в массиве pictures
      state.filterKeys.optionFilters.forEach(key => {
        result[key].sort((a, b) => (b.count - a.count));
      });
      commit('setAllOptions', result);
      commit('setOptionsQueue', []);
    },
    toggleOptions({commit, state, dispatch}, [key, value]) {
      const checkedOptions = Object.assign({}, state.checkedFilters);
      let optionsQueue = [...state.optionsQueue];
      // Определяет чекнута опция или нет. Чекнута – удаляет, иначе – добавляет
      const id = checkedOptions[key].indexOf(value);
      if (id < 0) {
        checkedOptions[key].push(value);
        optionsQueue.push({key, value})
      } else {
        checkedOptions[key].splice(id, 1);
        optionsQueue = optionsQueue.filter(option => option.value !== value);
      }
      // Сохраняет полученные результаты в state
      commit('setCheckedFilters', checkedOptions);
      commit('setOptionsQueue', optionsQueue);
      // Запускает филтрацию по обновленным условиям
      dispatch('__filterPictures');
    },
    changeRange({commit, state, dispatch}, {key, checkedId, min, max}) {
      const checkedFilters = Object.assign({}, state.checkedFilters);
      checkedFilters[key] = {checkedId, min, max};
      commit('setCheckedFilters', checkedFilters);
      dispatch('__filterPictures');
    },
    __filterPictures({commit, state}) {
      const pictures = state.pictures;
      const keyList = state.filterKeys.optionFilters;
      const rangeKey = state.filterKeys.rangeFilter;
      const checkedOptions = state.checkedFilters;
      // Поиск элементов в массиве картин отвечающих условиям
      const showedPictures = pictures.filter(picture => {
        return picture[rangeKey] >= checkedOptions[rangeKey].min
            && picture[rangeKey] <= checkedOptions[rangeKey].max
            && keyList.every(key => {
              let isKeyEmpty = true;
              return checkedOptions[key].some(option => {
                isKeyEmpty = false;
                return picture[key] === option;
              }) || isKeyEmpty;
            });
      });
      // Записывает результат в state
      commit('setShowedPictures', showedPictures);
    }
  },
  mutations: {
    setFilterKeys(state, filterKeys) {
      state.filterKeys = filterKeys;
    },
    setPictures(state, pictures) {
      state.pictures = pictures;
    },
    setShowedPictures(state, pictures) {
      state.showedPictures = pictures;
    },
    setAllOptions(state, options) {
      state.allOptions = options;
    },
    setCheckedFilters(state, options) {
      state.checkedFilters = options;
    },
    setOptionsQueue(state, options) {
      state.optionsQueue = options
    }
  },
  state: {
    // Исходный массив картин
    pictures: [],
    // Отображаемый массив кртин
    showedPictures: [],
    // Имена ключей по которым работает фильтр
    filterKeys: {},
    // Имена ключей для филтров по опциям и массив всех их значений из «pictures»
    allOptions: {},
    // Объект из «ключ – массив значений в состоянии checked»
    checkedFilters: {},
    // Массив из опцый фильтра в порядке их активирования пользователем
    optionsQueue: []
  },
  getters: {
    getShowedPictures(state) {
      return state.showedPictures;
    },
    getAllOptions(state) {
      return state.allOptions;
    },
    getCheckedFilters(state) {
      return state.checkedFilters;
    },
    getOptionsQueue(state) {
      return state.optionsQueue;
    }
  }
}
