<template>
  <section id="museums" class="museums">
    <h2 class="museums__heading">музеи</h2>
    <div class="museums__map">
            <yandexMap :coords="mapCenter" :zoom="14" :scroll-zoom="false" :behaviors="behaviors">
              <ymapMarker :coords="[59.9379176,30.3345569]" marker-id="marker-1"/>
            </yandexMap>
    </div>
    <ul class="museums__list">
      <li v-for="(item, id) in museumList" :key="id" class="museums__item">
        <h3 class="museums__name">
          <svg class="museums__icon" height="18" width="14" xmlns="http://www.w3.org/2000/svg">
            <path
                d="M7 0A6.63 6.63 0 00.95 9.33c1.66 3.63 4.84 7.46 5.77 8.54a.37.37 0 00.56 0c.93-1.08 4.11-4.91 5.77-8.54A6.63 6.63 0 007 0zm0 10.07a3.45 3.45 0 110-6.9 3.45 3.45 0 010 6.9z"
                fill="#202020"/>
          </svg>
          <a :href="item.href" class="museums__link">{{ item.name }}</a>
        </h3>
        <address class="museums__address">
          {{ item.city }}<br>
          {{ item.street }}<br>
          {{ item.phone }}
        </address>
      </li>
    </ul>
  </section>
</template>

<script>
import Vue from "vue";
import YmapPlugin from "vue-yandex-maps";
import {yandexMap, ymapMarker} from "vue-yandex-maps";
import museumList from "@/assets/data/museum-list"

const settings = {
  apiKey: '8f38c12f-36a2-4fd8-a4fb-f68f3f66f985',
  lang: 'ru_RU',
  coordorder: 'latlong',
  version: '2.1'
}

Vue.use(YmapPlugin, settings);

export default {
  name: "Museums",
  data() {
    return {
      mapCenter: [],
      behaviors: ['rightMouseButtonMagnifier', 'dblClickZoom', 'multiTouch'],
      museumList: museumList
    }
  },
  created() {
    this.setMapCenter();
    if (!('ontouchstart' in document.documentElement)) {
      this.behaviors.push('drag');
    }
  },
  methods: {
    setMapCenter: function () {
      this.mapCenter = window.innerWidth < 1024 ? [59.9312472, 30.3321377] : [59.9323357, 30.3120331];
    }
  },
  components: {yandexMap, ymapMarker}
}
</script>

