<template>
  <section id="biography" class="biography">
    <div class="wrapper">
      <div class="biography__container">
        <h2 class="biography__heading">биография</h2>
        <div>
          <LifeHistory :lifeHistoryList="lifeHistoryList"/>
          <LifeHistory v-if="isReadMore" :lifeHistoryList="lifeHistoryListPart2"/>
        </div>
      </div>
      <div class="biography__footer">
        <a class="biography__more" @click.prevent="moreHistoryClickHandler">
          <ReadMore :isReadMore="!isReadMore"/>
        </a>
        <Share :shareUrl="shareUrl" title="Биография Бориса Кустодиева"/>
      </div>
    </div>
  </section>
</template>

<script>
import lifeHistoryList from '@/assets/data/life-history-list.js';
import LifeHistory from './Life-history';
import ReadMore from './Read-more';
import Share from "./Share";

export default {
  name: "Biography",
  data: () => {
    return {
      lifeHistoryList: lifeHistoryList,
      lifeHistoryListPart2: [],
      isReadMore: false,
      shareUrl: location.origin + '/#biography'
    }
  },
  methods: {
    moreHistoryClickHandler: function () {
      if (this.lifeHistoryListPart2.length) {
        this.isReadMore = !this.isReadMore;
        return;
      }
      fetch('/data/life-history.json')
          .then(response => {
            if (response.ok) {
              return response.json();
            } else {
              throw new Error(`Response code ${response.status}`);
            }
          })
          .then(data => {
            this.lifeHistoryListPart2 = data.lifeHistory;
            this.isReadMore = true;
          })
          .catch(e => {
            console.log('Error fetch in Biography: ', e)
          })
    }
  },
  components: {Share, LifeHistory, ReadMore}
}
</script>

