<template>
  <div class="preview">
    <div
        :style="{transform: `translate3d(-${slideStep * getShow.startId}px, 0, 0)`, paddingLeft: `${slideStep * getShow.minId}px`}"
        class="preview__carriage"
        @click.capture.prevent="clickHandler">
      <a v-for="(item, id) in getShow.list" :key="id + getShow.minId"
         :class="{'preview__slide--active': activeId === id + getShow.minId}"
         :data-id="id + getShow.minId"
         class="preview__slide" href="#">
        <img :src="item.img" alt="" class="preview__img"/>
      </a>
    </div>
  </div>

</template>

<script>
import {mapActions, mapGetters} from "vuex";
import _ from "lodash";

export default {
  name: "Preview-slider",
  data() {
    return {
      activeId: 0,
      slideStep: 160
    }
  },
  methods: {
    ...mapActions(['slideTo']),
    clickHandler: _.throttle(function (evt) {
          const increment = parseInt(evt.target.dataset.id) - this.getShow.currentId;
          this.slideTo(increment);
        }, 500, {trailing: false}
    ),
    setActiveId: function () {
      this.activeId = this.getShow.currentId;
    }
  },
  computed: mapGetters(['getShow']),
  updated() {
    if (this.activeId !== this.getShow.currentId) {
      setTimeout(this.setActiveId, 0);
    }
  }
}
</script>

<style lang="scss">
@import '~@/assets/style/variables.scss';
@import '~@/assets/style/mixin.scss';

.preview {
  position: relative;
  width: 760px;
  height: 90px;
  overflow: hidden;

  @include mq(1023) {
    display: none;
  }

  &__carriage {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    transition-property: transform;
    transition-duration: $animation-duration;
    transition-timing-function: ease;
  }

  &__slide {
    position: relative;
    flex-shrink: 0;
    display: block;
    width: 120px;
    height: 90px;
    margin: 0 40px 0 0;
    user-select: none;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 1;
      background-color: rgba(0, 0, 0, 0.45);
      transition-property: opacity;
      transition-duration: $animation-duration;
      transition-timing-function: ease;
    }

    &--active::after {
      opacity: 0;
      pointer-events: none;
    }
  }

  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    object-fit: cover;
  }
}
</style>
