<template>
  <section class="pictures">
    <div class="pictures__container wrapper">
      <div class="pictures__name">{{ getInfoData.slideCapture }}</div>
      <div class="pictures__slider" @touchmove="touchmoveHandler" @touchstart="touchstartHandler">
        <Slider v-if="getInfoData.slidesTotal"/>
      </div>
      <div class="pictures__nav-control" @click.capture="buttonClickHandler">
        <button v-for="it in 2"
                :class="{
                'pictures__button--prev': it === 1,
                'pictures__button--disable': it === 1 && getInfoData.isLeftLimit || it === 2 && getInfoData.isRightLimit
            }"
                :data-increment="it * 2 - 3"
                class="pictures__button"
                type="button"
        >
          <svg class="pictures__icon-arrow" fill="#BFBFBF" height="12" width="41"
               xmlns="http://www.w3.org/2000/svg">
            <path
                d="M40.53 6.53c.3-.3.3-.77 0-1.06L35.76.7a.75.75 0 00-1.06 1.06L38.94 6l-4.24 4.24a.75.75 0 001.06 1.06l4.77-4.77zM0 6.75h40v-1.5H0v1.5z"/>
          </svg>
        </button>
      </div>
      <div class="pictures__number">{{ getInfoData.slideNumber }} / {{
          getInfoData.slidesTotal
        }}
      </div>
      <div class="pictures__preview">
        <PreviewSlider v-if="getInfoData.slidesTotal"/>
        <div class="pictures__linking">
          <RouterLink to="/paintings">
            все картины
            <span class="pictures__icon-corner">
              <svg fill="none" height="14" viewBox="0 0 14 14" width="14"
                   xmlns="http://www.w3.org/2000/svg">
                <path d="M6 10L9 7L6 4" stroke="#BFBFBF" stroke-width="1.2"/>
              </svg>
            </span>
          </RouterLink>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import PreviewSlider from "./Preview-slider";
import Slider from "./Slider";
import {mapActions, mapGetters} from "vuex";
import _ from "lodash";

// Колличестово видимых слайдов между центральным слайдом и краем болока
const CENTER_OFFSET = 2;
// Колличество видимых слайдов в окне болока
const SLIDES_IN_VIEW = 5;
// Url для ajax запроса.
const AJAX_URL = '/data/pictures.json';

export default {
  name: "Pictures",
  data() {
    return {
      startX: 0
    }
  },
  methods: {
    ...mapActions(['initSlider', 'slideTo']),
    scrollSlider: _.throttle(function (increment) {
      this.slideTo(increment)
    }, 500, {
      trailing:
          false
    }),
    buttonClickHandler(evt) {
      this.scrollSlider(evt.target.dataset.increment * 1);
    },
    touchstartHandler(evt) {
      this.startX = evt.touches[0].clientX;
    },
    touchmoveHandler: _.throttle(function (evt) {
      if (this.startX - evt.touches[0].clientX > 80) {
        this.startX = evt.touches[0].clientX;
        this.scrollSlider(1);
      } else if (this.startX - evt.touches[0].clientX < -80) {
        this.startX = evt.touches[0].clientX;
        this.scrollSlider(-1);
      }
    }, 30, {trailing: false})
  },
  computed: mapGetters(['getInfoData']),
  created() {
    this.initSlider({
      dataUrl: AJAX_URL,
      visibleCount: SLIDES_IN_VIEW,
      centerOffset: CENTER_OFFSET
    })
  },
  components: {Slider, PreviewSlider}
}
</script>

