<template>
  <div class="slider">
    <ul :style="{paddingLeft: `${getShow.minId * slideStep}px`,
    transform: `translate3d(-${getShow.currentId * slideStep}px, 0, 0)`}" class="slider__carriage">
      <li v-for="(item, id) in getShow.list" :key="id + getShow.minId"
          :class="{'slider__slide--active': activeId === id + getShow.minId}" class="slider__slide">
        <img :src="item.img" alt="" class="slider__img">
      </li>
    </ul>
  </div>
</template>

<script>
import {mapGetters} from "vuex";

export default {
  name: "Slider",
  data() {
    return {
      activeId: 0,
      slideStep: 220,
      isReady: true
    }
  },
  computed: mapGetters(['getShow']),
  methods: {
    setToActive() {
      this.activeId = this.getShow.currentId;
      this.isReady = true;
    }
  },
  updated() {
    if (this.activeId !== this.getShow.currentId && this.isReady) {
      this.isReady = false;
      setTimeout(this.setToActive, 50);
    }
  }
}
</script>

<style lang="scss">
@import '~@/assets/style/variables.scss';
@import '~@/assets/style/mixin.scss';

.slider {
  height: 530px;
  background-color: $black-medium;
  overflow: hidden;

  @include mq(1023) {
    width: 200px;
    height: 156px;
    margin: 0 auto;
    pointer-events: none;
    overflow: visible;
  }

  &__carriage {
    list-style: none;
    position: relative;
    margin: 0;

    @include mq(1024, 9999) {
      padding: 0 !important;
      transform: none !important;
    }

    @include mq(1023) {
      display: flex;
      transition-property: transform;
      transition-duration: $animation-duration;
    }
  }

  &__slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 920px;
    height: 530px;
    opacity: 0;
    transition-property: opacity;
    transition-duration: $animation-duration;
    transition-timing-function: ease;

    @include mq(1023) {
      position: relative;
      flex-shrink: 0;
      width: 200px;
      height: 156px;
      opacity: 1;
      transition-property: none;
      margin: 0 20px 0 0;
    }

    &::before {
      @include mq(1023) {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 1;
        background-color: rgba(0, 0, 0, 0.45);
        transition-property: opacity;
        transition-duration: $animation-duration;
        pointer-events: none;
      }
    }

    &--active {
      z-index: 1;
      opacity: 1;

      &::before {
        @include mq(1023) {
          opacity: 0;
        }
      }
    }
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
</style>
