        <rect height="2" rx="1" width="24" y="7"/>
        <rect height="2" rx="1" width="24" y="14"/>
      </svg>
    </button>
    <nav ref="nav" :style="{height: height}" class="header__nav">
      <Menu/>
    </nav>
  </header>
</template>

<script>
import Menu from './Menu.vue';
import Search from './Search';

export default {
  name: "Header",
  data() {
    return {
      height: `0`,
      isHidden: true
    }
  },
  methods: {
    buttonClickHandler() {
      this.isHidden = !this.isHidden;
      this.height = this.isHidden ? `0` : `${this.$refs.nav.scrollHeight}px`;
    }
  },
  components: {Menu, Search}
}
</script>

